.display__wrapper {
  display: flex;
  margin-top: 82px;
  width: 654px;
  min-height: 312px;
  background: #ffffff;
  border-radius: 37px;
  animation: fadeIn 0.3s;
  -webkit-animation: fadeIn 0.3s;
  box-shadow: 3px 4px 6px 1px rgba(0, 0, 0, 0.25);
}

.display__col1,
.display__col2 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  line-height: 43px;
  font-weight: bold;
}

.display__col1 {
  font-size: 36px;
  padding: 78px 0 40px 53px;
}

.display__col2 {
  font-size: 144px;
  line-height: 173px;
  padding: 64px;
  color: #29cb91;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100;
  }
}
